@import "../../globals.sass";
@import "~bootstrap/dist/css/bootstrap.min.css";

.header {
  position: fixed;
  display: flex;
  top: 0;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  width: 100%;
  box-shadow: 0px 0px 2px 2px lightgray;
  background-color: $header-color;
  height: $header-height;
  z-index: 1000;

  .saloon {
    @include main-text();

    font-weight: 600;
    float: left;
    margin-left: 35px;
    font-size: 2em;
  }

  li {
    list-style: none;
  }

  .login-account {
    @include main-text();

    font-weight: 300;
    font-size: 1.5em;
    flex: unset;
    margin-right: 35px;
    padding-left: 0.5em;

    &.btn-secondary,
    &.dropdown-toggle,
    &.btn {
      @include counter-bootstrap();

      margin-right: 50px;
      margin-left: 10px;
      color: $main-color !important;
      background: none !important;
    }

    .dropdown-item {
      outline: none;
    }
  }

  .search-wrapper {
    flex: 0.8;
    flex-shrink: 0.8;
    flex-direction: row;
    display: flex;
    justify-content: center;

    input {
      height: 2em;
      font-size: 1em;
      flex: 0.8;
      padding-left: 10px;
    }

    div {
      text-align: center;
      background-color: $main-color;
      padding: 4px;
      cursor: pointer;
    }

    .body {
      font-size: 0.2em;
    }
  }

  a {
    &:hover {
      color: $secondary-color !important;
    }
  }
}

a {
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}